// Could hold value vuex parts
import axiosInstance from "../../service/axios-instance";
import endpoints from "../../service/serviceEndpoints";
import { ACTION_RUN_REPORT } from "../../utilities/constants";

const state = {
  showReportLoader: true,
  reportData: {},
  selectedReportData: {},
  selectedReportRun: {},
};

const getters = {
  showReportLoader: (state) => {
    return state.showReportLoader;
  },
  reportData: (state) => {
    return state.reportData;
  },
  rowData: (state) => {
    return state.reportData.resources;
  },
  selectedReportData: (state) => {
    return state.selectedReportData;
  },
  selectedReportRun: (state) => {
    console.log("state.selectedReportRun: ", state.selectedReportRun);
    return state.selectedReportRun;
  },
};

const mutations = {
  toggleLoader: (state) => {
    state.showReportLoader = false;
  },
  updateReportData: (state, payload) => {
    state.reportData = payload;
  },
  setSelectedReportData: (state, payload) => {
    state.selectedReportData = payload;
  },
  setSelectedReportRun: (state, payload) => {
    state.selectedReportRun = payload;
  },
};

const actions = {
  callGetReports: ({ commit }) => {
    axiosInstance
      .get(endpoints.GET_REPORT_DETAILS)
      .then((res) => {
        console.log("res of " + endpoints.GET_REPORT_DETAILS + " : ", res.data);
        commit("toggleLoader");
        commit("updateReportData", res.data);
      })
      .catch((error) => console.log(error));
  },
  callGetSelectedReport({ commit }, payload) {
    return new Promise((resolve, reject) => {
      console.log("payload in callGetSelectedReport ", payload);
      commit("setSelectedReportData", payload);
      axiosInstance
        .get(payload.href + "/results?expand=resources")
        .then((res) => {
          let selectedReportRun = res.data.resources;
          commit("setSelectedReportRun", selectedReportRun);
          resolve();
        })
        .catch((error) => {
          console.log("error: ", error);
          reject(error);
        });
    });
  },
  callRunReport({ commit }, payload) {
    return new Promise((ressolve, reject) => {
      console.log("payload in callRunReport ", payload);
      axiosInstance
        .post(payload, ACTION_RUN_REPORT)
        .then((res) => {
          console.log("res of report run = ", res);
          ressolve();
        })
        .catch((error) => {
          console.log("error in report run: ", error);
          reject(error);
        });
    });
  },
};

export default {
  state,
  mutations,
  actions,
  getters,
};
