// Could hold value vuex parts
import axiosInstance from "../../service/axios-instance";
import endpoints from "../../service/serviceEndpoints";

const state = {
  showComputeLoader: true,
  ComputeInstanceData: {},
  terminatedCount: String,
  runningCount: String,
  stoppedCount: String,
  reserved: String,
  scheduled: String,
  spot: String,
  detached: String,
  privateDataset: [],
  familyDataSet: [],
  instanceTypeDataset: {},

};

const getters = {
  showComputeLoader: (state) => {
    return state.showComputeLoader;
  },
  computeInstanceData: (state) => {
    return state.instanceData;
  },
  terminatedCount: (state) => {
    return state.terminatedCount;
  },
  runningCount:(state) => {
      return state.runningCount; 
  },
  stoppedCount: (state) => {
      return state.stoppedCount;
  },
  reserved: (state) => {
    return state.reserved;
  },
  scheduled: (state) =>{
    return state.scheduled;
  },
  spot: (state) => {
    return state.spot;
  },
  detached: (state) => {
    return state.detached;
  },
  privateDataset: (state) => {
    return state.privateDataset;
  },
  familyDataSet: (state) => {
    return state.familyDataSet;
  },
  instanceTypeDataset : () =>{
    return state.instanceTypeDataset;
  }
};

const mutations = {
  toggleLoader: (state) => {
    state.showComputeLoader = false;
    console.log("inside showLoader", state.showComputeLoader);
  },
  updateInstanceData: (state, payload) => {
    console.log("payload in updateInstanceData", payload);
    state.computeInstanceData = payload;
    
    state.terminatedCount = payload.resources.filter(testing => testing.power_state == "terminated").length.toString();
    state.runningCount = payload.resources.filter(testing => testing.power_state == "on").length.toString();
    state.stoppedCount = payload.resources.filter(testing => testing.power_state == "off").length.toString();
  },
  updateReserved: (state,payload) => {
    state.reserved = payload.toString();
  },
  updateScheduled: (state,payload) => {
    state.scheduled = payload.toString();
  },
  updateSpot:(state,payload) => {
    state.spot = payload.toString();
  },
  updateDetached: (state,payload) => {
    state.detached = payload.toString();
  },

  updatePrivateDataset: (state,payload) => {
    console.log(payload);
    let dataSet= [
      {
        backgroundColor: ["#41B883", "#E46651"],
        data: [payload.public, payload.private],
      }
    ];
    let dataTemp= [];
    Object.keys(payload.family).forEach(value => {
      dataTemp.push(payload.family[value]);
    });
    console.log("dount",dataTemp);
    let instData =[
      {
        backgroundColor: ["#41B883", "#E46651", "#00D8FF", "#DD1B16", "#EE1BFF"],
        data: dataTemp
      }
    ]
    state.privateDataset = dataSet;
    state.familyDataSet = instData;
  },

  updateInstanceTypeDataset : (state,payload) => {
    
    let periods = [];
    let series = []
    payload.history.forEach(period => {
      periods.push(new Date(period.start).toLocaleString('en-us', { month: 'long' }));
    });

    for (let i = 0; i < periods.length; i++) {
      let serie = []
      for (let j = 0; j < periods.length; j++) {
        let item = payload.history[j].groups[i]
        if(item){
          serie.push({
            meta: item.key, value: item.amount.toFixed(2)
          })
        }else{
          serie.push({
            meta: 'others', value: 0
          })
        }
      }
      series.push(serie)
    }
    console.log("periods",periods);
    console.log("series",series);

    state.instanceTypeDataset = {
      labels : periods,
      series : series,
    }
    
  }
};

const actions = {
  callGetComputeInstanceDetails: ({ commit }) => {
    axiosInstance
      .get(endpoints.GET_INSTANCE_DETAILS)
      .then((res) => {
        console.log(
          "res of " + endpoints.GET_INSTANCE_DETAILS + " : ",
          res.data
        );
        commit("toggleLoader");
        commit("updateInstanceData", res.data);
        
      })
      .catch((error) => console.log(error));
    axiosInstance
      .get(endpoints.GET_RESEVED_EC2)
      .then((res) => {
        commit("updateReserved", res.data);
      })
      .catch((error) => console.log(error));
    axiosInstance
      .get(endpoints.GET_SCHEDULED_EC2)
      .then((res) => {
        commit("updateScheduled", res.data);
      })
      .catch((error) => console.log(error));
    axiosInstance
      .get(endpoints.GET_SPOT_EC2)
      .then((res) => {
        commit("updateSpot", res.data)
      })
      .catch((error) => console.log(error));
    axiosInstance
      .get(endpoints.GET_DETACHED_EIP)
      .then((res) => {
        commit("updateDetached", res.data);
      })
      .catch((error) => console.log(error));
  },
  callGetRegionDetails: ({commit}) => {
    axiosInstance
      .get(endpoints.GET_EC2_REGIONS)
      .then((res) =>{
        commit("updatePrivateDataset", res.data);
      })
      .catch((error)=> console.log(error));
  },
  callGetInstanceType: ({commit}) => {
    axiosInstance
      .get(endpoints.GET_INSTANCE_TYPE)
      .then((res) => {
        commit("updateInstanceTypeDataset", res.data);
      })
      .catch((error)=> console.log(error));
  }
};

export default {
  state,
  mutations,
  actions,
  getters,
};
