import Vue from "vue";
import Router from "vue-router";

// Containers
const TheContainer = () => import("@/components/container/TheContainer");

// Views
const Dashboard = () => import("@/views/dashboard/Dashboard");
const Widgets = () => import("@/views/widgets/Widgets");
const Compliance = () => import("@/views/compliance/Compliance");
const CostOptimizer = () => import("@/views/cost-optimizer/CostOptimizer");

const Compute = () => import("@/views/compute/ComputeWidgets");
const MyResources = () => import("@/views/my-resources/Resources");
const Report = () => import("@/views/report/Report");
const ScheduledReport = () => import("@/views/report/ScheduledReport");
const ServiceCatalog = () => import("@/views/service-catalog/ServiceCatalog");
const Resource = () => import("@/views/service-catalog/Resource");

// Views - Pages
const Page404 = () => import("@/views/pages/Page404");

const Page500 = () => import("@/views/pages/Page500");
const Login = () => import("@/views/pages/Login");
const Register = () => import("@/views/pages/Register");

// Users
const Users = () => import("@/views/users/Users");
const User = () => import("@/views/users/User");

Vue.use(Router);

export default new Router({
  mode: "history", // https://router.vuejs.org/api/#mode
  linkActiveClass: "active",
  scrollBehavior: () => ({ y: 0 }),
  routes: configRoutes(),
});

function configRoutes() {
  return [
    {
      path: "/",
      redirect: "/dashboard",
      name: "Home",
      component: TheContainer,
      children: [
        {
          path: "dashboard",
          name: "Dashboard",
          component: Dashboard,
        },
        {
          path: "compute",
          name: "Compute",
          component: Compute,
        },
        {
          path: "service-catalog",
          name: "Service Catalog",
          component: ServiceCatalog,
        },
        {
          path: "resource",
          name: "Resources",
          component: Resource,
        },
        {
          path: "widgets",
          name: "Widgets",
          component: Widgets,
        },
        {
          path: "my-resources",
          name: "My Resources",
          component: MyResources,
        },
        {
          path: "/compliance",
          name: "Compliance",
          component: Compliance,
        },
        {
          path: "/cost-optimizer",
          name: "Cost Optimizer",
          component: CostOptimizer,
        },

        {
          path: "report",
          meta: {
            label: "Report",
          },
          component: {
            render(c) {
              return c("router-view");
            },
          },
          children: [
            {
              path: "",
              name: "Report",
              component: Report,
            },
            {
              path: "scheduled-report",
              name: "Scheduled",
              component: ScheduledReport,
            },
          ],
        },

        {
          path: "users",
          meta: {
            label: "Users",
          },
          component: {
            render(c) {
              return c("router-view");
            },
          },
          children: [
            {
              path: "",
              name: "Users",
              component: Users,
            },
            {
              path: ":id",
              meta: {
                label: "User Details",
              },
              name: "User",
              component: User,
            },
          ],
        },
      ],
    },
    {
      path: "/pages",
      redirect: "/pages/404",
      name: "Pages",
      component: {
        render(c) {
          return c("router-view");
        },
      },
      children: [
        {
          path: "404",
          name: "Page404",
          component: Page404,
        },
        {
          path: "500",
          name: "Page500",
          component: Page500,
        },
        {
          path: "login",
          name: "Login",
          component: Login,
        },
        {
          path: "register",
          name: "Register",
          component: Register,
        },
      ],
    },
  ];
}
