// Could hold value vuex parts
import axiosInstance from "../../service/axios-instance";
import endpoints from "../../service/serviceEndpoints";
import { AUTH_STATUS } from "../../utilities/constants";
const state = {
  auth_token: "",
  status: "",
  auth_message: "",
};

const getters = {
  isAuthenticated: (state) => {
    return !!state.token;
  },
  authStatus: (state) => {
    return state.status;
  },
  authMessage: (state) => {
    return state.authMessage;
  },
};

const mutations = {
  setLoading: (state) => {
    state.status = AUTH_STATUS.loading;
  },
  setAuthMessage: (state, payload) => {
    state.authMessage = payload;
  },
  setAuthStatus: (state, payload) => {
    state.status = payload;
  },
  setAuthToken: (state, payload) => {
    console.log("payload inside setAuthToken: ", payload);
    state.auth_token = payload;
    // state.status = "Logged in";
  },
  logout: () => {
    // state.status = "logged out";
    localStorage.removeItem("user-token"); // clear your user's token from localstorage
    // delete axiosInstance.defaults.headers.common["X-Auth-Token"];
  },
};

const actions = {
  userLogin({ commit }, payload) {
    return new Promise((resolve, reject) => {
      console.log("payload in userLogin ", payload);

      commit("setLoading");
      axiosInstance
        .get(endpoints.AUTH_LOGIN, 
          // {
          // auth: {
          //   username: payload.username,
          //   password: payload.password,
          // },
        // }
        )
        .then((res) => {
          console.log("user login: ", res.data);
          const { auth_token } = res.data;
          localStorage.setItem("authToken", auth_token);
          // axiosInstance.defaults.headers.common["X-Auth-Token"] = auth_token;
          commit("setAuthToken", auth_token);
          commit("setAuthStatus", AUTH_STATUS.loggedIn);
          resolve();
        })
        .catch((error) => {
          console.log("errorrr: ", error);
          commit("setAuthStatus", AUTH_STATUS.invalidCred);
          commit("setAuthMessage", "Invalid Username or Password");
          reject(error);
        });
    });
  },

  userLogout({ commit }) {
    return new Promise((resolve, reject) => {
      commit("logout");
      commit("setAuthStatus", AUTH_STATUS.loggedOut);
      resolve();
    });
  },
};

export default {
  state,
  mutations,
  actions,
  getters,
};
