// Could hold value vuex parts
import axiosInstance from "../../service/axios-instance";
import endpoints from "../../service/serviceEndpoints";

const state = {
  showLoader: true,
  instanceData: {},
};

const getters = {
  showLoader: (state) => {
    return state.showLoader;
  },
  instanceData: (state) => {
    return state.instanceData;
  },
};

const mutations = {
  toggleLoader: (state) => {
    state.showLoader = false;
    console.log("inside showLoader", state.showLoader);
  },
  updateInstanceData: (state, payload) => {
    console.log("payload in updateInstanceData", payload);
    state.instanceData = payload;
  },
};

const actions = {
  callGetInstanceDetails: ({ commit }) => {
    axiosInstance
      .get(endpoints.GET_INSTANCE_DETAILS)
      .then((res) => {
        console.log(
          "res of " + endpoints.GET_INSTANCE_DETAILS + " : ",
          res.data
        );
        commit("toggleLoader");
        commit("updateInstanceData", res.data);
      })
      .catch((error) => console.log(error));
  },
};

export default {
  state,
  mutations,
  actions,
  getters,
};
