export const updateValue = (state, payload) => {
  state.value = payload;
};

export const toggleSidebarDesktop = (state) => {
  console.log("here in state.sidebarShow: ", state.sidebarShow);
  console.log("here in state.sidebarMinimize: ", state.sidebarMinimize);
  const sidebarOpened = [true, "responsive"].includes(state.sidebarShow);
  state.sidebarShow = sidebarOpened ? false : "responsive";
};

export const toggleSidebarMobile = (state) => {
  console.log("here in toggleSidebarMobile ", state);

  const sidebarClosed = [false, "responsive"].includes(state.sidebarShow);
  state.sidebarShow = sidebarClosed ? true : "responsive";
};

export const setView = (state, [variable, value]) => {
  state[variable] = value;
};
