// Could hold value vuex parts
import axiosInstance from "../../service/axios-instance";
import endpoints from "../../service/serviceEndpoints";

const state = {
  showCatalogLoader: true,
  catalogData: [],
  selectedCatalogId: "",
};

const getters = {
  showCatalogLoader: (state) => {
    return state.showCatalogLoader;
  },
  catalogData: (state) => {
    console.log("state.catalogData: ", state.catalogData);
    return state.catalogData;
  },
};

const mutations = {
  toggleLoader: (state) => {
    state.showCatalogLoader = false;
    console.log("inside showCatalogLoader", state.showCatalogLoader);
  },
  updateCatalogData: (state, payload) => {
    state.catalogData = payload.resources;
  },
  updateSelectedCatalogId: (state, payload) => {
    state.selectedCatalogId = payload;
  },
};

const actions = {
  callGetServiceCatalog: ({ commit }) => {
    axiosInstance
      .get(endpoints.GET_SERVICE_CATALOGS)
      .then((res) => {
        console.log(
          "res of " + endpoints.GET_SERVICE_CATALOGS + " : ",
          res.data
        );
        commit("toggleLoader");
        commit("updateCatalogData", res.data);
      })
      .catch((error) => console.log(error));
  },
};

export default {
  state,
  mutations,
  actions,
  getters,
};
