// Could hold value vuex parts
import axiosInstance from "../../service/axios-instance";
import endpoints from "../../service/serviceEndpoints";


const state = {
  showDashboardLoader: true,
  regionData: {},
  regionCount: String,
  currentCost: String,
  mostUsedServices: [],
  barDataSet: {},

};

const getters = {
  showDashboardLoader: (state) => {
    return state.showDashboardLoader;
  },
  regionData: (state) => {
    return state.regionData;
  },
  regionCount: (state) => {
    return state.regionCount;
  },
  currentCost: (state) => {
    return state.currentCost;
  },
  mostUsedServices: (state) =>{
    return state.mostUsedServices;
  },
  barDataSet: (state) =>{
    return state.barDataSet;
  }
};

const mutations = {
  toggleLoader: (state) => {
    state.showDashboardLoader = false;
    console.log("inside showLoader", state.showDashboardLoader);
  },
  updateInstanceData: (state, payload) => {
    console.log("payload in updateInstanceData", payload);
    state.regionData = payload.resources[0];
    
    state.regionCount = payload.resources.length.toString();
  },
  updateCurrentCost: (state, payload) => {
    state.currentCost = payload.toString();
  },
  updateMostUsedServices: (state, payload) => {
    payload[payload.length - 1].groups.slice(0 , 4).forEach(element => {
      state.mostUsedServices.push({
        name: element.key,
        cost: element.amount
      });
    });
    let periods = [];
    let series = []
    payload.forEach(period => {
      periods.push(new Date(period.start).toLocaleString('en-us', { month: 'long' }));
    });

    for (let i = 0; i < periods.length; i++) {
      let serie = []
      for (let j = 0; j < periods.length; j++) {
        let item = payload[j].groups[i]
        serie.push({
          meta: item.key, value: item.amount.toFixed(2)
        })
      }
      series.push(serie)
    }
    console.log("periods",periods);
    console.log("series",series);

    state.barDataSet = {
      labels : periods,
      series : series,
    }

    console.log(state.mostUsedServices);
  }
  
};

const actions = {
  callGetRegionsDetails: ({ commit }) => {
    axiosInstance
      .get(endpoints.GET_REGIONS_DETAILS)
      .then((res) => {
        commit("toggleLoader");
        commit("updateInstanceData", res.data);
        
      })
      .catch((error) => console.log(error));
  },
  callGetCurrentDetail: ({ commit }) => {
    axiosInstance
      .get(endpoints.GET_CURRENT_COST)
      .then((res) =>{
        commit("toggleLoader");
        commit("updateCurrentCost",res.data);
      })
      .catch((error)=> console.log(error));
  },
  callGetHistory: ({ commit }) => {
    axiosInstance
      .get(endpoints.GET_HISTORY)
      .then((res) =>{
        commit("toggleLoader");
        commit("updateMostUsedServices",res.data);
      })
      .catch((error)=> console.log(error));
  }
};

export default {
  state,
  mutations,
  actions,
  getters,
};
